  z-index: 20;
  -webkit-transform: translateX(0);
          transform: translateX(0);
  visibility: visible;
  opacity: 1;
  transition: all 0.5s ease-in-out;
  -webkit-animation: appear 0.5s;
          animation: appear 0.5s;
}

