.main-banner {
  position: relative;
  padding: 5rem 0rem;
  overflow: hidden;
}

.main-banner .title_name {
  position: absolute;
  top: 40%;
  left: -30px;
  opacity: 0.1;
  color: #f37335;
  font-weight: 600;
  font-size: 10rem;
}
.main-banner .banner-text {
  z-index: 1;
}

@media screen and (max-width: 992px) {
  .main-banner {
    background-color: #f2f7fd;
  }
  .banner-section.main-banner row .main-banner .banner-text {
    text-align: center;
  }
  .main-banner .banner-text form.search-form {
    margin: 0 auto;
  }
}

.main-banner .banner-text > h2 {
  color: #2b2b2b;
  font-size: 65.03px;
  line-height: 81.29px;
  font-weight: 600;
  margin-bottom: 26px;
  padding-right: 20px;
}

.main-banner .banner-text .search-form {
  max-width: 475px;
  position: relative;
  z-index: 1;
}

.main-banner .banner-text .search-form input {
  width: 100%;
  height: 50px;
  color: #575757;
  font-size: 14.23px;
  padding: 0 20px;
  border: 2px solid #f6f4f4;
  border-radius: 30px;
}

.main-banner .banner-text .search-form button {
  position: absolute;
  top: 50%;
  padding: 0;
  right: 20px;
  border: 0;
  color: #155799;
  background-color: inherit;
  transform: translateY(-50%);
  transition: all 0.4s ease-in-out;
}

.main-banner .banner-text p {
  color: #575757;
  font-size: 14.23px;
  line-height: 22.35px;
  max-width: 575px;
  margin-bottom: 59px;
}

.main-banner .banner-text > h2 span {
  display: inline-block;
  color: #f37335;
  position: relative;
}

.main-banner .elements-bg {
  position: absolute;
  top: 0;
  right: 0;
  z-index: -1;
  width: 536px;
  height: 596px;
  background-image: url(../../Assets/elements.png);
}
